<template>
  <div>
    <div v-on:click="nextPage(1)" class="relative w-full max-w-lg mx-auto" v-show="page==0">
      <img src="@/assets/main.jpg" class="w-full"/>
    </div>
    <div v-on:click="nextPage(2)" class="relative w-full max-w-lg mx-auto" v-show="page==1">
      <img src="@/assets/sub1.jpg" class="w-full"/>
    </div>
    <div v-on:click="nextPage(3)" class="relative w-full max-w-lg mx-auto" v-show="page==2">
      <img src="@/assets/sub2.jpg" class="w-full"/>
    </div>
    <div class="relative w-full max-w-lg mx-auto" v-show="page==3" id="copy">
      <div class="w-full max-w-sm p-4 bg-white border border-gray-200 rounded-lg shadow sm:p-6 md:p-8 dark:bg-gray-800 dark:border-gray-700">
          <form class="space-y-6" action="#">
              <div>
                  <label for="email" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">이름</label>
                  <input type="email" name="email" id="email" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-600 dark:border-gray-500 dark:placeholder-gray-400 dark:text-white">
              </div>
              <div>
                  <label for="password" class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">전화번호</label>
                  <input type="email" name="email" id="email" class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-600 dark:border-gray-500 dark:placeholder-gray-400 dark:text-white">
              </div>
              <button type="button" v-on:click="copy()" class="w-full text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800">제출</button>
          </form>
      </div>
      <button type="button" v-on:click="copy()" class="w-full focus:outline-none text-white bg-red-700 hover:bg-red-800 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-5 py-2.5 me-2 mb-2 dark:bg-red-600 dark:hover:bg-red-700 dark:focus:ring-red-900">처음페이지</button>

    </div>
    
  </div>
</template>

<script>
import download from 'downloadjs'
export default {
  name: 'MainPage',
  data: function(){
    return{
      page : 0
    }
  },
  methods: {
    nextPage(num){
      this.page = num;
    },
    copy: async function(){
      const copyEl = document.querySelector('#copy');
      const canvas = await this.$html2canvas(copyEl);
      const dataURL = canvas.toDataURL('image/png');
      download(dataURL, `demo.png`, 'image/png');
      this.page=0;
    }
  }
}
</script>
